<template>
  <div
    class="base-box"
    :class="{
      'base-box-computer': isComputer,
      'base-box-mobile': isMobile,
      'base-box-ipad': isIpad,
      'computer-show-aside': showAside,
      'classic': styleType === 'classic' && !isIpad,
      'business': styleType === 'business' && !isIpad,
      'simplicity': styleType === 'simplicity' && !isIpad
    }"
  >
    <Header :menu-list="menuList" :current-menu="currentMenu" @change="changeMenu"></Header>
    <Aside :menu-list="subMenuList"></Aside>
    <div class="content">
      <router-view />
    </div>
    <Bottom></Bottom>
  </div>
</template>

<script>
import Header from "./Header.vue";
import Aside from "./Aside.vue";
import Bottom from "./Bottom";

export default {
  name: "Base",
  components: { Header, Aside, Bottom },
  provide() {
    return { BaseLayout: this };
  },
  inject: ["screenType"],
  props: {},
  data() {
    return {
      showAside: true,
      styleType: "simplicity", // classic: ; business: ; simplicity: ;
      menuList: [
        // {
        //   id: "1",
        //   title: "",
        //   icon: "el-icon-user",
        //   name: "personal",
        //   path: "/personal",
        //   children: [{}]
        // },
        // { id: "2", title: "", icon: "el-icon-coin" },
        // {
        //   id: "3",
        //   title: "",
        //   name: "operation", // header
        //   path: "/operation",
        //   icon: "el-icon-monitor",
        //   children: [
        //     {
        //       title: "",
        //       icon: "el-icon-monitor",
        //       path: "/operation/host-manage",
        //       children: [
        //         {
        //           title: "",
        //           icon: "el-icon-coin",
        //           path: "/operation/host-manage/device-group"
        //         },
        //         { title: "", icon: "el-icon-monitor", path: "" },
        //         { title: "", icon: "el-icon-document-checked", path: "" }
        //       ]
        //     },
        //     {
        //       title: "",
        //       icon: "el-icon-document-copy",
        //       path: "",
        //       children: [
        //         {
        //           title: "",
        //           icon: "el-icon-coin",
        //           path: ""
        //         },
        //         {
        //           title: "",
        //           icon: "el-icon-coin",
        //           path: ""
        //         },
        //         {
        //           title: "",
        //           icon: "el-icon-coin",
        //           path: ""
        //         }
        //       ]
        //     }
        //   ]
        // },
        // { id: "4", title: "", icon: "el-icon-notebook-1" },
        // { id: "5", title: "", icon: "el-icon-notebook-2" },
        // { id: "6", title: "", icon: "el-icon-weixiu" },
        // { id: "7", title: "", icon: "el-icon-s-operation" },
        // { id: "8", title: "", icon: "el-icon-warning-outline" },
        // { id: "9", title: "", icon: "el-icon-s-data" },
        // { id: "10", title: "", icon: "el-icon-picture-outline-round" },
        // { id: "11", title: "", icon: "el-icon-picture-outline-round" }
      ],
      subMenuList: [],
      currentMenu: ""
    };
  },
  computed: {
    isComputer() {
      return this.screenType === "computer";
    },
    isMobile() {
      return this.screenType === "mobile";
    },
    isIpad() {
      return this.screenType === "ipad";
    }
  },
  watch: {},
  created() {
    const styleFlagCatch = sessionStorage.getItem("layoutStyleFlagCatch");
    this.styleType = styleFlagCatch || "classic";
  },
  mounted() {
    this.setCurrentMenu();
    if (this.screenType === "mobile") {
      this.showAside = false;
    } else {
      this.showAside = true;
    }
    this.getMenuList().then(
      res => {
        this.menuList = res.data.navigationsList;
        this.setCurrentMenu();
      },
      rej => {}
    );
  },
  beforeRouteUpdate(to, from, next) {
    const path = to.path;
    this.menuList.forEach((item, i) => {
      if (item.name && path.indexOf(item.name) != -1) {
        this.subMenuList = item.children;
      }
    });
    next();
  },
  methods: {
    getMenuList() {
      return new Promise((resolve, reject) => {
        this.$remote.post("user/profile").then(
          res => {
            if (res.code === 20000) {
              console.log(res);
              resolve(res);
            } else {
              reject("");
            }
          },
          rej => {
            reject(rej);
          }
        );
      });
    },

    changeStyle(style) {
      this.styleType = style;
      sessionStorage.setItem("layoutStyleFlagCatch", style);
    },

    changeMenu(menu) {
      if (menu.path) {
        this.subMenuList = menu.children;
        this.currentMenu = menu.id;
        this.$router.push(menu.path);
      }
    },

    setCurrentMenu() {
      const path = this.$router.history.current.path;
      this.menuList.forEach((item, i) => {
        if (item.path && path.indexOf(item.path) != -1) {
          this.subMenuList = item.children;
          this.currentMenu = item.id;
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
.base-box {
  width: 100%;
  height: 100%;
  background-color: #153c66;
  transition: 0.5s all ease;
  .content {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    background-color: #153c66;
    border: 1px solid #2e6fa7;
  }
}
// 1
.base-box.classic {
  background-color: #153c66;
  .content {
    background-color: #153c66;
  }
}
.base-box-computer.classic {
  padding: 80px 0 25px 0px;
}
.base-box-computer.classic.computer-show-aside {
  padding-left: 200px;
}
// 2
.base-box.business {
  background-color: #edeff3;
  .content {
    background-color: #edeff3;
  }
}
.base-box-computer.business {
  padding: 0 0 25px 60px;
}
.base-box-computer.business.computer-show-aside {
  padding-left: 260px;
}
// 3
.base-box.simplicity {
  background-color: #375d77;
  .content {
    background-color: #375d77;
    background: linear-gradient(to right, #366c9b 0%, #5ba7ad 100%);
  }
}
.base-box-computer.simplicity {
  padding: 40px 0 25px 0px;
}
.base-box-computer.simplicity.computer-show-aside {
  padding-left: 200px;
}

// 
.base-box-mobile {
  width: 100%;
  height: 100%;
  padding: 0;
  padding-top: 40px;
  padding-bottom: 25px;
  background-color: #153c66;
}
// ipad
.base-box-ipad {
  width: 100%;
  height: 100%;
  padding: 60px 0 0;
  background-color: #eeeeee;
  .content {
    background-color: #eeeeee;
    border: none;
  }
}
</style>
<style lang="less">
// 
.info-modular-row {
  display: block;
}
.info-modular-row-flex {
  display: flex;
}
// 1
.base-box.classic {
  color: white;
  .info-modular-box {
    padding: 5px;
    .info-modular {
      height: 100%;
      border-radius: 5px;
      border: 1px solid #2e6fa7;
      overflow: hidden;
      .title {
        color: white;
        font-size: 12px;
        line-height: 30px;
        padding: 0 24px;
        background-color: #2e6fa7;
      }
    }
  }
  // el-tree
  .el-tree {
    .el-tree-node {
      background-color: #153c66;
      color: white;
      line-height: 30px;
    }
    .is-current > .el-tree-node__content {
      background-color: rgb(132, 169, 248);
    }
    .el-tree-node__content:hover {
      background-color: rgb(73, 73, 73);
    }
    .el-tree-node__content:focus {
      background-color: rgb(13, 14, 15);
    }
  }
  // tabs
  .el-tabs {
    .el-tabs__item {
      color: white;
    }
    .el-tabs__item.is-active {
      color: rgb(132, 169, 248);
    }
  }
  // el-progress
  .el-progress {
    .el-progress__text {
      color: white;
    }
  }
  // el-input
  .el-input {
    .el-input__inner {
      background-color: #153c66;
      color: white;
    }
  }
}
// 2
.base-box.business {
  color: #333333;
  .info-modular-box {
    padding: 5px;
    .info-modular {
      height: 100%;
      border: 1px solid #333333;
      overflow: hidden;
      .title {
        color: #333333;
        font-size: 16px;
        line-height: 30px;
        padding: 0 12px;
      }
    }
  }
  // tabs
  .el-tabs {
    .el-tabs__item.is-active {
      border-bottom-color: #409eff;
    }
  }
}
// 3
.base-box.simplicity {
  color: white;
  .info-modular-box {
    padding: 5px;
    border: 1px solid #bdd9f1;
    .info-modular {
      height: 100%;
      overflow: hidden;
      .title {
        color: white;
        font-size: 16px;
        line-height: 30px;
        padding: 0 12px;
      }
    }
  }
  // el-tree
  .el-tree {
    .el-tree-node {
      background-color: #3c769e;
      color: white;
      line-height: 30px;
    }
    .is-current > .el-tree-node__content {
      background-color: rgb(132, 169, 248);
    }
    .el-tree-node__content:hover {
      background-color: rgb(73, 73, 73);
    }
    .el-tree-node__content:focus {
      background-color: rgb(13, 14, 15);
    }
  }
  // tabs
  .el-tabs {
    .el-tabs__item {
      color: white;
    }
    .el-tabs__item.is-active {
      color: rgb(132, 169, 248);
    }
  }
  // el-progress
  .el-progress {
    .el-progress__text {
      color: white;
    }
  }
  // el-input
  .el-input {
    .el-input__inner {
      background: none;
      color: white;
    }
  }
}
// 4 
.base-box.base-box-ipad {
  .info-modular-box {
    padding: 5px;
    .info-modular {
      height: 100%;
      border-radius: 5px;
      border: none;
      background-color: rgba(0, 0, 0, 0.3);
      overflow: hidden;
      .title {
        color: white;
        font-size: 12px;
        line-height: 30px;
        padding: 0 24px;
        background-color: rgba(0, 0, 0, 0.6);
      }
    }
  }
}
</style>